import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Row, Card, Form, DatePicker, Input } from 'antd';
import { createOutRoomStudents } from 'services/inOutStudent';
import { dateToString } from 'utils/formatUtil';
import { getQueryParams } from 'utils/urlUtil';

const { TextArea } = Input;
@Form.create({ name: 'formOutStudent' })
class FormOutStudent extends Component {
  state = {
    studentId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { id: queryParamId } = getQueryParams();
    if (queryParamId) {
      this.setState({ studentId: queryParamId });
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { studentId } = this.state;
    const dateValue = {
      ...values,
      'date-picker': values['date-picker'].format('YYYY-MM-DD'),
    };
    try {
      await createOutRoomStudents({
        studentId,
        startDate: dateToString(dateValue['date-picker'], 'YYYY/MM/DD'),
        address: values.address,
        phoneNo: values.phoneNo,
        houseLord: values.houseLord,
        note: values.note,
      });
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const config = {
      rules: [{ type: 'object', message: 'Please select time!' }],
    };
    return (
      <Card>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit}>
            {/* <Form.Item hasFeedback label="Mã Sinh Viên" {...formItemLayout}>
              {getFieldDecorator('studentCode', {
                initialValue: initialValue.studentCode,
                rules: [
                  {
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input disabled />)}
            </Form.Item> */}
            {/* <Form.Item hasFeedback label="Họ và Tên" {...formItemLayout}>
              {getFieldDecorator('name', {
                initialValue: initialValue.name,
                rules: [
                  {
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input disabled />)}
            </Form.Item> */}
            <Form.Item {...formItemLayout} label="Từ ngày">
              {getFieldDecorator('date-picker', config)(<DatePicker />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Địa chỉ" hasFeedback>
              {getFieldDecorator('address', {
                initialValue: initialValue.address,
                rules: [
                  {
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<TextArea rows={8} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Điện thoại" hasFeedback>
              {getFieldDecorator('phoneNo', {
                initialValue: initialValue.phoneNo,
                rules: [
                  {
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên chủ hộ" hasFeedback>
              {getFieldDecorator('houseLord', {
                initialValue: initialValue.houseLord,
                rules: [
                  {
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Ghi chú" hasFeedback>
              {getFieldDecorator('note', {
                initialValue: initialValue.note,
                rules: [
                  {
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<TextArea rows={8} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormOutStudent {...props} />
  </Page>
));



